<template>
    <div>
      <el-form ref="form" :model="formData" :rules="formRules" label-width="120px">
        <el-form-item label="二手车年龄" prop="age">
          <el-input v-model="formData.age" placeholder="请输入二手车年龄"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')">价格预测</el-button>
        </el-form-item>
      </el-form>
      <div v-if="prediction !== null">
        预测价格为: {{ prediction }}
      </div>
    </div>
  </template>
  
  <script> 
  export default {
    data() {
      return {
        formData: {
          age: '',
        },
        formRules: {
          age: [
            { required: true, message: '请输入二手车年龄', trigger: 'blur' },
          ],
        },
        prediction: null,
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$axios
              .post('/api/predict/', { age: this.formData.age })
              .then((response) => {
                this.prediction = response.data.prediction;
                this.$message({
                  message: '价格预测成功！',
                  type: 'success',
                });
              })
              .catch((error) => {
                console.error(error);
                this.$message.error('价格预测失败！');
              });
          } else {
            return false;
          }
        });
      },
    },
  };
  </script>
  